const fusion = () => {
  const ul = document.querySelector('.flexblock.features ul')
  ul.className = 'flexblock features'

  const fullscreen = document.querySelector('.fullscreen')
  if (fullscreen !== null) {
    fullscreen.getElementsByTagName('div')[0].className = ''
  }
}

const cardBuilder = (card) => {
  const className = card.substring(card.indexOf('card-'), 7)
  const img = document.querySelector('.' + className + ' img')
  const cardNode = document.createElement('div')
  cardNode.setAttribute('class', className)
  let figure = document.createElement('figure')
  wrap(img, figure)
}

const wrap = (el, wrapper) => {
  el.parentNode.insertBefore(wrapper, el)
  wrapper.appendChild(el)
}

exports.fusion = fusion
exports.cardBuilder = cardBuilder
