const rotateIn = () => {
  const animate = document.getElementsByClassName('rotateIn')
  for (let i = 0; i < animate.length; i++) {
    let content = ''
    for (let x = 0; x < animate[i].textContent.length; x++) {
      let char = animate[i].textContent.charAt(x)
      if (char !== ' ') {
        content += `<span>${char}</span>`
      } else {
        content += `<span style="width:10px;height:4px">${char}</span>`
      }
    }
    animate[i].innerHTML = content
  }
}

export { rotateIn }
