import { Marpit, Element } from '@marp-team/marpit'
import hljs from 'highlight.js'
import 'highlight.js/styles/isbl-editor-light.css'
import { pipeAnimation } from './prisma-home'
import { rotateIn } from './animation'
import { fusion, cardBuilder } from './marp-webslides'

const fs = require('fs')

const marpit = new Marpit({
  container: [new Element('article', { id: 'webslides' })],
})

hljs.initHighlightingOnLoad()

marpit.markdown.renderer.rules.marpit_slide_open = (t, i, opts, _, self) =>
  `${self.renderToken(t, i, opts)}<div class="wrap size-80">`

marpit.markdown.renderer.rules.marpit_slide_close = () => '</div></section>'

const markdown = fs.readFileSync('presentation.md', 'UTF-8').trim()

marpit.customDirectives.local.card = (value, marpit) => {
  return { class: `card-${value}`, style: 'div' }
}

const { html } = marpit.render(markdown)

document.getElementById('main').innerHTML = html

// Initialize WebSlides
require('webslides')
const ws = new WebSlides()

const pipe = document.getElementById('page-background-right')
const footer = document.getElementById('footer')

ws.el.addEventListener('ws:slide-change', (slide) => {
  if (slide.detail.currentSlide !== 1) {
    pipe.hidden = true
    footer.hidden = false
    const elementClass = slide.srcElement.firstChild.className
    if (elementClass.includes('fuchsia')) {
      footer.getElementsByTagName('img')[0].className = 'whitelogo'
    } else {
      footer.getElementsByTagName('img')[0].className = ''
      footer.setAttribute('class', 'fadeIn slow')
    }
  } else {
    pipeAnimation()
    pipe.hidden = false
    footer.hidden = true
  }
})

if (ws.currentSlide_.i === 0) {
  pipeAnimation()
  footer.hidden = true
} else {
  pipe.hidden = true
  footer.hidden = false
  footer.setAttribute('class', 'fadeIn slow')
}

fusion()
rotateIn()

if (document.className) {
  cardBuilder(elementClass)
}
