import anime from 'animejs/lib/anime.es.js'

const pipeAnimation = () => {
  let ends = document.getElementsByClassName('pipe-end')
  for (let i = 0; i < ends.length; i++) {
    ends[i].setAttribute('opacity', 0)
  }
  anime({
    targets: '.pipe-start path',
    strokeDashoffset: [anime.setDashoffset, 0],
    easing: 'easeInSine',
    duration: 1000,
    delay: function (el, i) {
      return i * 250
    },
  })
  console.log('Animate wait:')
  setTimeout(function () {
    let ends = document.getElementsByClassName('pipe-end')
    for (let i = 0; i < ends.length; i++) {
      ends[i].setAttribute('opacity', 1)
    }
    anime({
      targets: '.pipe-end path',
      strokeDashoffset: [anime.setDashoffset, 0],
      easing: 'easeInSine',
      duration: 1500,
      delay: function (el, i) {
        return i * 250
      },
    })
  }, 1200)
}

exports.pipeAnimation = pipeAnimation
